.header {
  width: 100%;

  background-color: white;

  border-bottom: 2px solid rgba(0, 0, 0, 0.468);
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.468);
  padding: 2.4rem 2.4rem;

  position: fixed;
  z-index: 99;

  display: flex;
  align-items: center;
  justify-content: center;

  @media (min-width: 768px) {
    width: 100%;
    height: 80px;

    background-color: transparent;

    padding: 0 3.6rem;

    justify-content: space-between;
  }

  &::before {
    @media (min-width: 768px) {
      width: 100%;
      height: 100%;

      content: "";
      display: block;
      background-color: black;
      opacity: 0.7;

      position: absolute;
      top: 0;
      left: 0;

      z-index: 0;
    }
  }

  .header__hamburger {
    @media (min-width: 768px) {
      display: flex;
      align-items: center;
    }

    .fa-bars,
    .fa-times {
      color: black;
      cursor: pointer;
      font-size: 3rem;
      padding: 2.4rem;
      position: absolute;
      margin: auto;
      top: 50%;
      left: 0;
      transform: translateY(-50%);

      @media (min-width: 768px) {
        color: white;

        z-index: 1;
      }
    }
  }

  .header__logo {
    color: black;
    opacity: 1;

    display: flex;
    flex-direction: column;
    align-items: center;

    h6 {
      font-size: 1.4rem;
      font-weight: 900;
    }

    h1 {
      font-size: 2.4rem;
      font-weight: 900;
    }

    @media (min-width: 768px) {
      width: 200px;

      color: white;

      position: absolute;
      margin: auto;
      right: 0;
      left: 0;
    }
  }

  .header__socials {
    color: black;
    font-size: 2.5rem;
    z-index: 1;

    @media (min-width: 768px) {
      color: white;
    }

    .fa-twitter {
      margin-left: 3rem;
    }
  }

  .header__book-btn {
    border: 1px solid white;
    border-radius: 2rem;
    position: fixed;
    margin: auto;
    bottom: 10px;
    right: 10px;

    font-size: 2rem;
    padding: 1.6rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (min-width: 768px) {
      width: 60px;
      height: 60px;

      border-radius: 2rem;
      cursor: pointer;

      position: fixed;
      margin: 0;
      top: unset;
      right: 15px;
      bottom: 15px;

      z-index: 1;
    }

    @media (min-width: 1280px) {
      width: 90px;
      height: 90px;
    }

    &::before {
      width: 100%;
      height: 100%;

      content: "";
      display: block;
      border-radius: 2rem;
      background-color: black;
      opacity: 0.4;
      position: absolute;

      z-index: 0;
    }

    a {
      text-decoration: none;
      z-index: 1;

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .fa-book-open {
      color: white;

      @media (min-width: 768px) {
        font-size: 3rem;
        z-index: 1;
      }
    }

    .book-text {
      display: none;

      @media (min-width: 1280px) {
        display: block;
        color: white;

        padding-top: 0.5rem;
        font-size: 1.6rem;
        font-weight: 900;
        z-index: 1;
      }
    }
  }
}

